.tickets {
    padding: $separation 0;
    h3 {
        text-align: center;
        color: $white;
    }

    .container-tickets{
        margin-top: $separation / 2;
        @include grid(2, $separation)
    }

    .plan {
        p {
            color: $white;
            font-size: 3rem;
            text-align: center;
            margin-bottom: 2rem;
        }
    }
}

.pass {
    width: 100%;
    border-spacing: 0;
    thead{
        background-color: $pink;
        th {
            padding: $separation / 4;
            color: $white;
            text-transform: uppercase;
            font-weight: $slim;
            font-size: 2rem
            ;
        }
    }

    tbody {

        background-color: $white;
        td {
            padding: $separation / 4;
            border-bottom: 1px solid #e1e1e1
            ;
        }

        tr {
            &:last-of-type {
                td{
                    border: none;
                }
            }
        }
    }

    tfoot {
        background-color: $purple;

        td {
            padding: $separation / 4;
            text-align: center;
            font-size: 3rem;
            color: $white;
        }
    }
}